function Logo (props) {
  const cStyle = {
    'color' : props.color,
    'font-family' : '"Open Sans", sans serif',
    'font-size' : props.fontSize + 'px',
    'margin-top' : '0',
    'margin-bottom' : '0',
  }
  const pStyle = {
    'color' : props.color,
    'font-family' : '"Open Sans", sans serif',
    'font-size' : props.fontSize + 'px',
    'margin-top' : '0',
    'margin-bottom' : '0',
    'margin-left' : ((parseInt(props.fontSize)*0.4)*-1).toString() + 'px',
  }
  const yStyle = {
    'color' : props.color,
    'font-family' : '"Open Sans", sans serif',
    'font-size' : parseInt(props.fontSize)-(props.fontSize*0.05) + 'px',
    'margin-top' : props.fontSize*0.05 + 'px',
    'margin-bottom' : '0',
    'margin-left' : ((parseInt(props.fontSize)*0.71)*-1).toString() + 'px',
    'padding-bottom' : -'10px'
  }
  const divStyle = {
    'display' : 'flex',
    'flex-direction' : 'row'
    /*'margin-left' : '-25px'*/
  }
  return (
    <div style={divStyle}>
      <p style={cStyle}>C</p>
      <p style={pStyle}>P</p>
      <p style={yStyle}>Y</p>
    </div>
  )
}

function Project(props) {
  const containerStyle = {
    'border-radius' : '15px',
    'box-shadow' : '5px 5px 10px #001000FF, -5px -5px 10px #001000FF',
    'width' : '90%',
    'padding' : '10px',
    'display' : 'flex',
    'flex-direction' : 'column',
    'justify-content' : 'center',
    'margin' : '15px'
  }

  const logoStyle = {
    'width' : '70px',
    'height' : '70px'
  }

  let languageIcons = [];

  for(let i = 0; i < props.langs.length; i++) {
    languageIcons.push(
      <object data={props.langs[i]} style={logoStyle} />
    );
  }

  const textStyle = {
    'text-align' : 'center',
    'font-size' : '30px',
    'margin' : '5px',
    'color' : 'whitesmoke',
    'text-decoration' : 'none'
  }

  const langsDivStyle = {
    'display' : 'flex',
    'flex-direction' : 'row',
    'justify-content' : 'space-evenly',
    'padding' : '5px',
    'width' : '50%',
    'align-items' : 'center',
    'align-self' : 'center'
  }

  function hover(e) {
    e.target.style.textDecoration = 'underline';
  }

  function notHover(e) {
    e.target.style.textDecoration = 'none';
  }

  return(
    <div style={containerStyle}>
      <a onMouseOver={hover}
         onMouseOut={notHover}
         href={props.link}
         style={textStyle}>
        {props.name}
      </a>

      <div style={langsDivStyle}>
        {languageIcons}
      </div>

      <p style={textStyle}>
        {props.description}
      </p>
    </div>
  )
}

