function App() {
  const [page, show] = useState(<HomePage/>)

  function showHome() {
    show(<HomePage/>)
  }

  function showSkills() {
    show(<Skills/>)
  }

  function showProjects() {
    show(<Projects/>)
  }

  function showAbout() {
    show(<About/>)
  }

  function getWindowSize() {
    const {innerWidth, innerHeight} = window;
    return {innerWidth, innerHeight};
  }

  function NavBar () {
    const [windowSize, _] = useState(getWindowSize());

    const divStyle = {
      'display' : 'flex',
      'flex-direction' : 'row',
      'justify-content' : (windowSize.innerHeight > windowSize.innerWidth) && ! isBrowser ? 'space-between' : 'space-evenly',
      'align-items' : 'center',
      'position' : 'sticky',
      'top' : '0',
      'background-color' : '#172016',
      'box-shadow' : '0px 3px 15px #001000FF',
      'border-radius' : '15px',

    }

    const aStyle = {
      'text-decoration' : 'none',
      'font-size' : '20px',
      'color' : 'white',
      'width' : (windowSize.innerHeight > windowSize.innerWidth) && ! isBrowser ? 'auto' : '10%',
      'text-align' : 'center',
      'align-self' : 'center',
      'margin' : (windowSize.innerHeight > windowSize.innerWidth) && ! isBrowser ? '10px' : '0'
    }

    const logoDiv = {
      'display' : 'flex',
      'flex-direction' : 'row',
      'justify-content' : 'center',
    }

    const style = {

    }

    return (
      <div style={divStyle}>
        <a href="#" onClick={showHome} style={aStyle}>
          <div style={logoDiv}>
            <Logo fontSize="40" color="white"/>
          </div>
        </a>
        <a href="#" onClick={showSkills} style={aStyle}>Skills</a>
        <a href="#" onClick={showProjects} style={aStyle}>Projects</a>
        <a href="#" onClick={showAbout} style={aStyle}>About Me</a>
      </div>
    )
  }

  const style = {
    minWidth : isBrowser ? '820px' : 'auto'
  }

  /**/

  return (
    <div style={style} className="mainDiv">
      <NavBar />
      {page}
    </div>
  );
}

export default App;
